
@import url('/static/css/mixin.css');
html,
body,
#app{
 height:100%
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter,
.fade-leave-to {
  opacity: 0
}
#box{
height:100%;
position:relative;
}
.left {
  position: absolute;
  text-align: center;
  top: 60%;
  left: 50%;
  margin-left: -160px;
  margin-top: -160px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.right {
	height: 100%;
	background: transparent;
	position: relative;
}



